'use client';

import Image from 'next/image';
import { Pencil, Trash2, Loader2, Search, ChevronLeft, ChevronRight, AlertTriangle } from 'lucide-react';
import { Button } from '@/components/ui/button';
import {
    Table,
    TableBody,
    TableCell,
    TableHead,
    TableHeader,
    TableRow,
} from '@/components/ui/table';
import { BahanBaku } from '@/types';

interface InventarisTabelProps {
    loading: boolean;
    bahans: BahanBaku[];
    totalData: number;
    currentPage: number;
    itemsPerPage: number;
    onPageChange: (page: number) => void;
    onEdit: (item: BahanBaku) => void;
    onDelete: (id: number) => void;
}

export default function InventarisTabel({
    loading,
    bahans,
    totalData,
    currentPage,
    itemsPerPage,
    onPageChange,
    onEdit,
    onDelete
}: InventarisTabelProps) {
    
    const totalPages = Math.ceil(totalData / itemsPerPage);
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;

    return (
        <div className="rounded-md border bg-white shadow-sm">
            <Table>
                <TableHeader>
                    <TableRow className="bg-gray-50">
                        <TableHead className="w-[50px] text-center">No</TableHead>
                        <TableHead className="w-[80px]">Foto</TableHead>
                        <TableHead>Nama Bahan</TableHead>
                        <TableHead>Kategori</TableHead>
                        <TableHead>Stok Saat Ini</TableHead>
                        <TableHead>Min. Stok</TableHead>
                        <TableHead className="text-right">Aksi</TableHead>
                    </TableRow>
                </TableHeader>
                <TableBody>
                    {loading ? (
                        <TableRow>
                            <TableCell colSpan={7} className="h-24 text-center text-gray-500">
                                <Loader2 className="h-5 w-5 animate-spin inline mr-2" /> Memuat...
                            </TableCell>
                        </TableRow>
                    ) : bahans.length === 0 ? (
                        <TableRow>
                            <TableCell colSpan={7} className="h-32 text-center text-gray-500">
                                <div className="flex flex-col items-center justify-center">
                                    <Search className="h-8 w-8 text-gray-300 mb-2" />
                                    <p>Tidak ada bahan baku yang cocok.</p>
                                </div>
                            </TableCell>
                        </TableRow>
                    ) : (
                        bahans.map((item, index) => {
                            // Cek Stok Kritis
                            const isCritical = Number(item.stok_saat_ini) <= Number(item.batas_stok_minimum);
                            
                            return (
                                <TableRow key={item.id} className={`hover:bg-gray-50 ${isCritical ? 'bg-red-50' : ''}`}>
                                    <TableCell className="text-center text-gray-500">
                                        {(currentPage - 1) * itemsPerPage + index + 1}
                                    </TableCell>
                                    <TableCell>
                                        <div className="relative h-10 w-10 rounded-md overflow-hidden bg-gray-100 border">
                                            {item.foto_url ? (
                                                <Image src={item.foto_url} alt={item.nama_bahan_baku} fill className="object-cover" unoptimized onError={(e) => (e.target as HTMLElement).style.display = 'none'} />
                                            ) : <div className="w-full h-full flex items-center justify-center text-xs">IMG</div>}
                                        </div>
                                    </TableCell>
                                    <TableCell className="font-medium">{item.nama_bahan_baku}</TableCell>
                                    <TableCell>
                                        <span className="inline-flex items-center px-2 py-0.5 rounded text-xs font-medium bg-blue-50 text-blue-700">
                                            {item.kategori?.nama_kategori}
                                        </span>
                                    </TableCell>
                                    <TableCell>
                                        <div className="flex items-center gap-2">
                                            <span className={`font-bold ${isCritical ? 'text-red-600' : 'text-gray-900'}`}>
                                                {Number(item.stok_saat_ini)} {item.satuan?.nama_satuan}
                                            </span>
                                            {isCritical && <AlertTriangle className="h-4 w-4 text-red-500 animate-pulse" />}
                                        </div>
                                    </TableCell>
                                    <TableCell className="text-gray-500">
                                        {Number(item.batas_stok_minimum)} {item.satuan?.nama_satuan}
                                    </TableCell>
                                    <TableCell className="text-right space-x-2">
                                        <Button variant="ghost" size="icon" onClick={() => onEdit(item)}>
                                            <Pencil className="h-4 w-4 text-blue-600" />
                                        </Button>
                                        <Button variant="ghost" size="icon" onClick={() => onDelete(item.id)}>
                                            <Trash2 className="h-4 w-4 text-red-600" />
                                        </Button>
                                    </TableCell>
                                </TableRow>
                            );
                        })
                    )}
                </TableBody>
            </Table>

            {!loading && totalData > 0 && (
                <div className="flex items-center justify-between p-4 border-t">
                    <div className="text-sm text-gray-500">
                         Total: {totalData} Item
                    </div>
                    <div className="flex items-center space-x-2">
                        <Button variant="outline" size="sm" onClick={() => onPageChange(currentPage - 1)} disabled={currentPage === 1}>
                            <ChevronLeft className="h-4 w-4" />
                        </Button>
                        <span className="text-sm font-medium px-2">Hal {currentPage} / {totalPages}</span>
                        <Button variant="outline" size="sm" onClick={() => onPageChange(currentPage + 1)} disabled={currentPage === totalPages}>
                            <ChevronRight className="h-4 w-4" />
                        </Button>
                    </div>
                </div>
            )}
        </div>
    );
}