'use client';

import { useState, useEffect, FormEvent } from 'react';
import { Loader2 } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import {
    Select,
    SelectContent,
    SelectItem,
    SelectTrigger,
    SelectValue,
} from "@/components/ui/select";
import {
    Dialog,
    DialogContent,
    DialogFooter,
    DialogHeader,
    DialogTitle,
} from '@/components/ui/dialog';
import { BahanBaku, Kategori, Satuan } from '@/types';

interface InventarisFromDialogProps {
    isOpen: boolean;
    onOpenChange: (open: boolean) => void;
    onSubmit: (formData: any, isEdit: boolean) => Promise<void>;
    initialData: BahanBaku | null;
    kategoris: Kategori[];
    satuans: Satuan[];
}

export default function InventarisFormDialog({
    isOpen,
    onOpenChange,
    onSubmit,
    initialData,
    kategoris,
    satuans
}: InventarisFromDialogProps) {
    const [isSaving, setIsSaving] = useState(false);
    const [formData, setFormData] = useState({
        nama_bahan_baku: '',
        kategori_id: '',
        satuan_id: '',
        stok_saat_ini: '',
        batas_stok_minimum: '',
        foto: null as File | null,
    });

    useEffect(() => {
        if (isOpen && initialData) {
            setFormData({
                nama_bahan_baku: initialData.nama_bahan_baku,
                kategori_id: initialData.kategori_id.toString(),
                satuan_id: initialData.satuan_id.toString(),
                stok_saat_ini: initialData.stok_saat_ini.toString(),
                batas_stok_minimum: initialData.batas_stok_minimum.toString(),
                foto: null
            });
        } else if (isOpen && !initialData) {
            setFormData({
                nama_bahan_baku: '',
                kategori_id: '',
                satuan_id: '',
                stok_saat_ini: '',
                batas_stok_minimum: '',
                foto: null
            });
        }
    }, [isOpen, initialData]);

    const handleSubmit = async (e: FormEvent) => {
        e.preventDefault();
        setIsSaving(true);
        await onSubmit(formData, !!initialData);
        setIsSaving(false);
    };

    return (
        <Dialog open={isOpen} onOpenChange={onOpenChange}>
            <DialogContent className="sm:max-w-[600px]">
                <DialogHeader>
                    <DialogTitle>{initialData ? 'Edit Bahan Baku' : 'Tambah Bahan Baku'}</DialogTitle>
                </DialogHeader>
                <form onSubmit={handleSubmit} className="space-y-4 mt-2">
                    <div className="grid grid-cols-2 gap-4">
                        <div className="space-y-2 col-span-2">
                            <Label>Nama Bahan <span className="text-red-500">*</span></Label>
                            <Input value={formData.nama_bahan_baku} onChange={(e) => setFormData({...formData, nama_bahan_baku: e.target.value})} required />
                        </div>
                        <div className="space-y-2">
                            <Label>Kategori <span className="text-red-500">*</span></Label>
                            <Select value={formData.kategori_id} onValueChange={(val) => setFormData({...formData, kategori_id: val})}>
                                <SelectTrigger><SelectValue placeholder="Pilih..." /></SelectTrigger>
                                <SelectContent>
                                    {kategoris.map((k) => <SelectItem key={k.id} value={k.id.toString()}>{k.nama_kategori}</SelectItem>)}
                                </SelectContent>
                            </Select>
                        </div>
                        <div className="space-y-2">
                            <Label>Satuan <span className="text-red-500">*</span></Label>
                            <Select value={formData.satuan_id} onValueChange={(val) => setFormData({...formData, satuan_id: val})}>
                                <SelectTrigger><SelectValue placeholder="Pilih..." /></SelectTrigger>
                                <SelectContent>
                                    {satuans.map((s) => <SelectItem key={s.id} value={s.id.toString()}>{s.nama_satuan}</SelectItem>)}
                                </SelectContent>
                            </Select>
                        </div>
                        <div className="space-y-2">
                            <Label>Stok Awal <span className="text-red-500">*</span></Label>
                            <Input type="number" value={formData.stok_saat_ini} onChange={(e) => setFormData({...formData, stok_saat_ini: e.target.value})} required />
                        </div>
                        <div className="space-y-2">
                            <Label>Batas Minimum (Alert)</Label>
                            <Input type="number" value={formData.batas_stok_minimum} onChange={(e) => setFormData({...formData, batas_stok_minimum: e.target.value})} />
                        </div>
                    </div>
                    <div className="space-y-2">
                        <Label>Foto</Label>
                        <Input type="file" accept="image/*" className="cursor-pointer" onChange={(e) => { if (e.target.files?.[0]) setFormData({ ...formData, foto: e.target.files[0] }); }} />
                    </div>
                    <DialogFooter className="pt-4">
                        <Button type="button" variant="outline" onClick={() => onOpenChange(false)}>Batal</Button>
                        <Button type="submit" disabled={isSaving} className="bg-[#D93F21] hover:bg-[#b9351b]">
                            {isSaving && <Loader2 className="mr-2 h-4 w-4 animate-spin" />} Simpan
                        </Button>
                    </DialogFooter>
                </form>
            </DialogContent>
        </Dialog>
    );
}