'use client';

import { Plus } from 'lucide-react';
import { Button } from '@/components/ui/button';

// Import Custom Hook
import { useInventoryPage } from '@/hooks/admin/useInventoryPage';

// Import Komponen UI (Sesuai nama path import Anda sebelumnya)
import InventoryFilters from '@/components/admin/inventaris/InventarisFilter';
import InventoryTable from '@/components/admin/inventaris/InventarisTabel';
import InventoryFormDialog from '@/components/admin/inventaris/InventarisFormDialog';

export default function InventoryPage() {
    // Panggil Logika dari Hook
    const {
        // Data
        bahans,
        totalData,
        loading,
        kategoris,
        satuans,
        
        // Filter & Pagination
        currentPage,
        itemsPerPage,
        searchQuery,
        setSearchQuery,
        filterKategori,
        setFilterKategori,
        setCurrentPage,
        
        // Modal State
        isDialogOpen,
        setIsDialogOpen,
        editData,
        
        // Handlers
        handleCreate,
        handleEdit,
        handleDelete,
        handleSubmit
    } = useInventoryPage();

    return (
        <div className="space-y-6">
            {/* Header Section */}
            <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
                <div>
                    <h1 className="text-2xl font-bold text-gray-900">Stok Bahan Baku</h1>
                    <p className="text-gray-500 text-sm">Kelola inventaris dapur dan bar.</p>
                </div>
                
                <Button 
                    className="bg-[#D93F21] hover:bg-[#b9351b] shadow-sm" 
                    onClick={handleCreate}
                >
                    <Plus className="w-4 h-4 mr-2" /> Tambah Bahan
                </Button>
            </div>

            {/* Filter Bar */}
            <InventoryFilters 
                searchQuery={searchQuery}
                setSearchQuery={setSearchQuery}
                filterKategori={filterKategori}
                setFilterKategori={setFilterKategori}
                kategoris={kategoris}
            />

            {/* Tabel Data */}
            <InventoryTable 
                loading={loading}
                bahans={bahans}
                totalData={totalData}
                currentPage={currentPage}
                itemsPerPage={itemsPerPage}
                onPageChange={setCurrentPage}
                onEdit={handleEdit}
                onDelete={handleDelete}
            />

            {/* Modal Form */}
            <InventoryFormDialog 
                isOpen={isDialogOpen}
                onOpenChange={setIsDialogOpen}
                onSubmit={handleSubmit}
                initialData={editData}
                kategoris={kategoris}
                satuans={satuans}
            />
        </div>
    );
}